@import '../../../App.scss';

.skill-group {
  display: flex;
  flex-direction: column;
  position: absolute;
  align-items: center;
  transform: scaleY(calc(1 / 0.6));

  .skill-circle {
    position: relative;
    display: flex;
    border: 1px solid $lightgrey;
    border-radius: 50%;
    height: 96px;
    min-width: 96px;
    max-width: 96px;
    justify-content: center;
    background: $black;

    .certificate-icon {
      position: absolute;
      left: 75%;
      color: $white;
    }

    &:hover {
      background: $lightgrey;
      color: $darkgrey;
      transition: ease-out 0.2s;
      box-shadow: 0 0 30px $lightgrey;

      .brand-icon {
        opacity: 0;
        transition: ease-out 0.2s;
      }

      .hover-name {
        opacity: 1;
        transition: ease-in 0.2s;
      }

      + .hover-experience {
        opacity: 1;
        transition: ease-in 0.2s;
      }
    }

    .brand-icon {
      height: 64px;
      align-self: center;
      filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(85deg)
        brightness(109%) contrast(101%);
      opacity: 0.8;
    }

    .hover-name {
      font-size: 14px;
      font-weight: 500;
      letter-spacing: 1px;
      opacity: 0;
      align-self: center;
      position: absolute;
    }
  }

  .hover-experience {
    color: $white;
    font-weight: 500;
    letter-spacing: 1px;
    margin-top: 1rem;
    opacity: 0;
    text-align: center;
    width: max-content;
  }
}

@media screen and (max-width: 1024px) {
  .skill-group {
    display: flex;
    position: relative;
    transform: none;
    height: 100%;

    .hover-experience {
      width: auto;
      opacity: 1;
    }
  }
}
